import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';

@Component({
  selector: 'lib-angularCustom',
  templateUrl: "./angular-custom.component.html",
  styleUrls: ["./angular-custom.component.scss"
  ]
})
export class AngularCustomComponent implements OnInit {
  text: string = ""
  @Input("isError") isError: any;
  @Input("placeholder") placeholder: any;
  @Input("errorMessage") errorMessage: any;
  @Output() getData: EventEmitter<any> = new EventEmitter();
  data: any;
  constructor() { }

  ngOnInit(): void {
  }

  getchange(event: any) {
    this.data = event.target.value
    this.invokeEvent(this.data);
  }

  invokeEvent(data) {
    this.getData.emit(data);
  }
}
